<template>
  <div id="app">
    <div v-if="false" style="position:fixed">{{ scrollTop }}</div>
    <div class="loading" v-if="loading">
      <i class="fa fa-spin fa-circle-o-notch" style="font-size:6rem"></i>
    </div>
    <h1 class="mb-3">COVID-19 Tracker</h1>
    <h3 class="my-4" :style="headStyle('h3')">
      Worldwide cases (last
      <input type="number" v-model="daysShown" min="1" :max="daysInStats" />
      day{{ daysShown == 1 ? "" : "s" }})
      &nbsp;
      <a class="fa fa-info-circle" title="Data source" target="_blank" href="https://github.com/pomber/covid19" style="text-decoration:none"></a>
    </h3>
    <div>
      <table>
        <thead :style="headStyle()">
          <tr>
            <td></td>
            <td
              v-for="(d,k) in coronaData[Object.keys(coronaData)[0]]"
              :key="k"
              v-show="k >= startIndex"
            >{{ (new Date(d.date)).toString().substr(4,6) }}</td>
            <td></td>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(c,i) in Object.keys(coronaData)" :key="i">
            <td>{{ c }}</td>
            <td v-for="(d,j) in coronaData[c]" :key="j" v-show="j >= startIndex">
              <div class="confirmed" title="confirmed">{{ d.confirmed }}</div>
              <div class="deaths" title="deaths">{{ d.deaths }}</div>
              <div class="recovered" title="recovered">{{ d.recovered }}</div>
            </td>
            <td>
              <div class="confirmed_pc" title="confirmed">{{ percentage(i, "confirmed") }}</div>
              <div class="deaths_pc" title="deaths">{{ percentage(i, "deaths") }}</div>
              <div class="recovered_pc" title="recovered">{{ percentage(i, "recovered") }}</div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,
      coronaData: {},
      daysShown: 14,
      daysInStats: 62,
      scrollTop: 0
    };
  },

  computed: {
    startIndex() {
      return this.daysInStats - this.daysShown;
    }
  },

  methods: {
    percentage(i, type) {
      var c = Object.keys(this.coronaData)[i];
      var last_stat = this.coronaData[c][this.daysInStats - 1][type];
      var first_stat = this.coronaData[c][this.daysInStats - this.daysShown][
        type
      ];

      if (first_stat == 0) {
        return "N/A";
      }
      return Math.round(((last_stat - first_stat) / first_stat) * 100) + "%";
    },

    onscroll() {
      this.scrollTop = window.scrollY;
    },

    headStyle(h3) {
      if (h3) {
        return this.scrollTop > 200
          ? {
              position: "fixed",
              top: "0",
              width: 150 + (this.daysInStats - this.daysShown) * 1.65 + "px",
              marginLeft: "20px",
              background: "#fff"
            }
          : { position: "static", top: "auto" };
      }

      return this.scrollTop > 200
        ? { position: "fixed", top: "0" }
        : { position: "static", top: "auto" };
    }
  },

  mounted() {
    document.addEventListener("scroll", this.onscroll);

    this.loading = true;
    fetch("https://pomber.github.io/covid19/timeseries.json")
      .then(r => r.json())
      .then(data => {
        this.coronaData = data;
        this.loading = false;
      });
  }
};
</script>

